<template>
    <div class="full-fit">
        <div class="full-fit">
            <div style="display:inline-block; width: 49%; height: 100%">
                <div class="input-title">Stack</div>
                <label>
                    <textarea name="textarea" class="input-field" @change="onChange('left')" v-model="leftArea"></textarea>
                </label>
            </div>
            <div class="input-title" style="display:inline-block; width: 48%; height: 100%; margin-left: 1%">
                <div class="input-title">T-SQL</div>
                <label>
                    <textarea name="textarea" class="input-field" @change="onChange('right')" v-model="rightArea"></textarea>
                </label>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "StackToSqlApp",
        data() {
            return {
                leftArea: "",
                rightArea: ""
            }
        },
        methods: {
            stackSqlToTSql: function (stackSql) {
                const result = [];
                let tablePart = false;
                for (let i = 0; i < stackSql.length; i++) {
                    const c = stackSql.charAt(i);
                    switch (c) {
                        case "~":
                            if (tablePart) {
                                result.push("]")
                            } else {
                                result.push("stack.[")
                            }
                            tablePart = !tablePart;
                            break;
                        default:
                            result.push(c);
                    }
                }
                return result.join("")
            },
            tSqlToStackSql: function (tSql) {
                return tSql
            },
            onChange(event) {
                if (event === 'left') {
                    this.rightArea = this.stackSqlToTSql(this.leftArea)
                } else {
                    this.leftArea = this.tSqlToStackSql(this.rightArea)
                }
                console.log(event)
            }
        }
    }
</script>

<style scoped>
    .input-field {
        width: 100%;
        height: 90%;
        resize: none;
        background-color: #000000;
        color: white;
    }

    .full-fit {
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
    }

    .input-title {
        justify-content: center;
        align-items: center;
        display: flex;
        font-size: 19px;
        height: 7%;
    }
</style>