<template>
    <div>
        <div v-if="activeButtonId !== id" class="left-menu-button" v-on:click="onClick"> {{ label }}</div>
        <div v-if="activeButtonId === id" class="left-menu-button-active"> {{ label }}</div>
    </div>
</template>

<script>
    export default {
        name: "LeftMenuButton",
        props: ["label", "activeButtonId", "id"],
        methods: {
            onClick: function () {
                this.$emit('clicked', this.id)
            }
        }
    }
</script>

<style scoped>

    .left-menu-button {
        transition: 0.2s;
        cursor: pointer;
        justify-content: center;
        align-items: center;
        display: flex;
        height: 40px;
        color: white;
        background-color: #686868;
        width: 100%;
    }

    .left-menu-button:hover {
        transition: 0.2s;
        cursor: pointer;
        justify-content: center;
        align-items: center;
        display: flex;
        height: 40px;
        color: #000000;
        background-color: #afafaf;
        width: 104%;
    }

    .left-menu-button-active {
        transition: 0.2s;
        cursor: pointer;
        justify-content: center;
        align-items: center;
        display: flex;
        height: 40px;
        color: #000000;
        background-color: #ffffff;
        width: 104%;
    }
</style>